<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png">
    <HelloWorld msg="Welcome to the interactive fiction" />
    <h1>{{ msg }}</h1>
    <p>
      This is an interactive fiction web.<br>
      Here you can read interactive fiction of all genres, interact and create your own fiction with other readers.<br>
      You can click on the navigation bar above to move to the section you want to go to. <br>
    </p>
    <h3>Popular Novels</h3>
    <ul>
      <li><button @click="getStory1">随机小故事1</button></li>
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src


export default {
  name: 'HomeView',
  methods: {
    getStory1() {
      const url = '/story.txt';
      fetch(url)
        .then(response => response.text())
        .then(text => {
          this.$router.push({
            name: 'reader',
            params: { story: text },
          });
        })
        .catch(error => {
          console.error('Error fetching story:', error);
        });
    }


  }
}
</script>
